___CSS_LOADER_EXPORT___.push([module.id, `* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

@media screen and (min-width:800px) {
    #content {
        border-radius: 10px;
        box-shadow: 0 0 20px black;
    }

    body {
        padding: 50px 80px;
        background-color: purple;
    }
}

body {
    height: 100dvh;
    width: 100dvw;
}

#content {
    background: #d1d1d1;
    height: 100%;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}

#hero {
    height: 100px;
    width: 100%;
    background-color: rgb(252, 131, 71);
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    display: flex;
}

#hero>h1 {
    margin: auto 0 auto 20px;
    padding: 10px;
    color: white;
}

#navul {
    list-style: none;
    display: flex;
    width: 160px;
    flex-direction: column;
}

@media screen and (min-width:800px) {
    #navbar {
        background-color: #e1e1e1;
        width: 160px;
        border-bottom-left-radius: 10px;
    }
}

#navbar {
    height: calc(100% - 100px);
    background-color: #e1e1e1;
    width: 160px;
}

.navdiv {
    display: flex;
    justify-content: space-between;
    width: 160px;
    height: 50px;
    padding: 10px 20px 10px 20px;
    font-weight: 900;
    font-size: 20px;
    font-family: Arial, Helvetica, sans-serif;
}

.navdiv:hover {
    background-color: #fff;
}

.navlink {
    text-decoration: none;
    color: black;
}

.addbtn {
    all: unset;
    cursor: pointer;
}

#modalWrapper {
    display: none;
    z-index: 10;
    background-color: rgba(82, 80, 80, 0.449);
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

#pcvd {
    height: 20px;
    width: 20px;
    transition: transform 200ms ease-in-out;
}


.AdderModalContainer {
    background-color: #fff;
    border-radius: 10px;
    position: absolute;
    z-index: 11;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 400px;
    width: 600px;
}

.adderHero {
    background-color: rgb(255, 140, 0);
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    padding: 20px;
    color: white;
}

#projAdderinpsContainer {
    overflow: auto;
    height: calc(100% - 78px);
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    padding: 10px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

.adderContainer {
    height: calc(100% - 78px);
    display: flex;
    flex-wrap: wrap;
    padding: 20px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

textarea {
    padding: 4px 10px;
    border: none;
    resize: none;
    display: block;
    width: 100%;
    font-size: 20px;
}

#projtextareaContainer {
    min-height: 130px;
}

textarea:focus {
    outline: none;
}

.adderbtn {
    all: unset;
    text-align: center;
    margin-top: auto;
    width: 100%;
    background-color: #ffffff00;
    cursor: pointer;
    border: 1px solid orangered;
    border-radius: 5px;
}

.projtasks {
    border-radius: 10px;
    background-color: black;
    padding: 10px;
    margin: 10px 0px;
}

.precedenceSelect {
    margin: auto 10px 10px 10px;
    border-radius: 5px;
    width: 200px;
    height: 35px;
    font-size: 20px;
    text-align: center;
    background-color: green;
    color: white;
}

.ProjpreSel {
    margin: 10px 10px 10px 0px;
    width: 200px;
    height: 35px;
    font-size: 20px;
    text-align: center;
}

.projDueDate {
    margin: 10px 10px 10px 0px;
    font-size: 20px;
    height: 35px;
}

.dueDate {
    margin: auto 10px 10px 10px;
    border-radius: 5px;
    font-size: 20px;
    height: 35px;
}

option {
    text-align: center;
}

#addmoretasks {
    margin: 10px 0px;
}

#projectbtn {
    all: unset;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 20px 10px 20px;
    width: 160px;
    height: 50px;
    font-weight: 900;
    font-size: 20px;
    font-family: Arial, Helvetica, sans-serif;
    cursor: pointer;
}

#projectbtn:hover {
    background-color: #fff;
}

@keyframes ulfadeinout {
    from {
        opacity: 0;
        height: 0;
    }

    to {
        opacity: 1;
        height: fit-content;
    }
}

#projectsul {
    list-style: none;
}

.projulhidden {
    display: none;
    opacity: 0;
}

.projulshown {
    display: block;
    animation: ulfadeinout 200ms forwards;
}

.projnavlink {
    background-color: #f1f1f1;
    display: block;
    text-decoration: none;
    color: black;
    width: 160px;
    height: 40px;
    font-size: 20px;
    padding: 10px 10px 10px 40px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.projnavlink:hover {
    background-color: #fff;
}

.projCompcl {
    width: calc(100% - 160px);
}

.projttl {
    width: 100%;
    background-color: #fff;
    padding: 10px;
    text-align: center;
}

.projdes {
    width: 100%;
    background: #fcfcfc;
    padding: 10px;
}

.projtaskscontainerdiv {
    background: #f9f9f9;
}

.taskdiv {
    display: flex;
    align-items: center;
    padding: 10px;
}

.checkbox {
    margin-right: 20px;
}

.tasktitle {
    margin: 0px 20px 0px 0px;
    font-weight: 900;
    font-family: Arial, Helvetica, sans-serif;
}

.taskdldate {
    border: 1px solid #a1a1a1;
    padding: 5px;
    border-radius: 5px;
    margin: 0px 20px 0px auto;

}

.detailstaskbtn {
    padding: 5px;
    margin-right: 10px;
}

.deltaskbtn,
.edittaskbtn {
    all: unset;
    box-sizing: border-box;
    height: 30px;
    width: 30px;
    padding: 5px;
    cursor: pointer;

}


.deltaskbtn:hover .deleteicon {
    fill: red;
}

.edittaskbtn:hover .editicon,
.edittaskbtn:hover .editiconPath2 {

    fill: rgb(10, 250, 10);
}

#week,
#today,
#home,
#notes {
    width: calc(100% - 160px);
    background-color: #fff;
}

#week>.taskdiv,
#today>.taskdiv,
#home>.taskdiv {
    margin-top: 5px;
    background-color: #f1f1f1;
}

.notesdiv {
    width: 50%;
    margin: 5px;
    background-color: #f1f1f1;
}

.notestitlediv {
    display: flex;
    align-items: center;
}

.notestitle {
    padding: 10px;
    font-size: 20px;
}

.notestitlediv>.editnotesbtn {
    margin: 10px 10px 10px auto;
}

.notestitlediv>.delnotesbtn {
    margin-right: 10px;
}

.notesdescription {
    padding: 10px;
}`, "",{"version":3,"sources":["webpack://./src/assets/css/style.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;;AAEA;IACI;QACI,mBAAmB;QACnB,0BAA0B;IAC9B;;IAEA;QACI,kBAAkB;QAClB,wBAAwB;IAC5B;AACJ;;AAEA;IACI,cAAc;IACd,aAAa;AACjB;;AAEA;IACI,mBAAmB;IACnB,YAAY;IACZ,WAAW;IACX,aAAa;IACb,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,WAAW;IACX,mCAAmC;IACnC,4BAA4B;IAC5B,6BAA6B;IAC7B,aAAa;AACjB;;AAEA;IACI,wBAAwB;IACxB,aAAa;IACb,YAAY;AAChB;;AAEA;IACI,gBAAgB;IAChB,aAAa;IACb,YAAY;IACZ,sBAAsB;AAC1B;;AAEA;IACI;QACI,yBAAyB;QACzB,YAAY;QACZ,+BAA+B;IACnC;AACJ;;AAEA;IACI,0BAA0B;IAC1B,yBAAyB;IACzB,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,YAAY;IACZ,YAAY;IACZ,4BAA4B;IAC5B,gBAAgB;IAChB,eAAe;IACf,yCAAyC;AAC7C;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,qBAAqB;IACrB,YAAY;AAChB;;AAEA;IACI,UAAU;IACV,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,WAAW;IACX,yCAAyC;IACzC,kBAAkB;IAClB,MAAM;IACN,SAAS;IACT,OAAO;IACP,QAAQ;AACZ;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,uCAAuC;AAC3C;;;AAGA;IACI,sBAAsB;IACtB,mBAAmB;IACnB,kBAAkB;IAClB,WAAW;IACX,QAAQ;IACR,SAAS;IACT,gCAAgC;IAChC,aAAa;IACb,YAAY;AAChB;;AAEA;IACI,kCAAkC;IAClC,6BAA6B;IAC7B,4BAA4B;IAC5B,aAAa;IACb,YAAY;AAChB;;AAEA;IACI,cAAc;IACd,yBAAyB;IACzB,aAAa;IACb,sBAAsB;IACtB,iBAAiB;IACjB,aAAa;IACb,+BAA+B;IAC/B,gCAAgC;AACpC;;AAEA;IACI,yBAAyB;IACzB,aAAa;IACb,eAAe;IACf,aAAa;IACb,+BAA+B;IAC/B,gCAAgC;AACpC;;AAEA;IACI,iBAAiB;IACjB,YAAY;IACZ,YAAY;IACZ,cAAc;IACd,WAAW;IACX,eAAe;AACnB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,UAAU;IACV,kBAAkB;IAClB,gBAAgB;IAChB,WAAW;IACX,2BAA2B;IAC3B,eAAe;IACf,2BAA2B;IAC3B,kBAAkB;AACtB;;AAEA;IACI,mBAAmB;IACnB,uBAAuB;IACvB,aAAa;IACb,gBAAgB;AACpB;;AAEA;IACI,2BAA2B;IAC3B,kBAAkB;IAClB,YAAY;IACZ,YAAY;IACZ,eAAe;IACf,kBAAkB;IAClB,uBAAuB;IACvB,YAAY;AAChB;;AAEA;IACI,0BAA0B;IAC1B,YAAY;IACZ,YAAY;IACZ,eAAe;IACf,kBAAkB;AACtB;;AAEA;IACI,0BAA0B;IAC1B,eAAe;IACf,YAAY;AAChB;;AAEA;IACI,2BAA2B;IAC3B,kBAAkB;IAClB,eAAe;IACf,YAAY;AAChB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,UAAU;IACV,sBAAsB;IACtB,aAAa;IACb,mBAAmB;IACnB,8BAA8B;IAC9B,4BAA4B;I